<template>
  <section class="edit-item-container">
    <form class="edit-form" @submit.prevent="saveItem">
      <div>
        <img :src="currItem.img">
      </div>
      <div >
            <label>
            <span>Title:</span>
            <input type="text" v-model="currItem.title" required>
          </label>
          <label>
            <span>Category:</span>
            <select v-if="catagories.length>0" v-model="currItem.category">
              <option v-for="catagory in catagories" :key="catagory" :value="catagory">{{catagory}}</option>
            </select>
          </label>
          <label>
            <span>Condition:</span>
            <select v-model="currItem.condition">
              <option value="likeNew">Like new</option>
              <option value="used">Used</option>
            </select>
          </label>
          <label>
            <span>Description:</span>
            <textarea cols="30" rows="10" v-model="currItem.desc" required></textarea>
          </label>
          <label>
            <span>Price:</span>
            <input type="number" v-model="currItem.price" required>
          </label>
      
      <button type="submit">Save</button>
      </div>

    </form>
    <garage-footer></garage-footer>
  </section>
</template>

<script>
import garageHeader from "../components/garage-header.vue";
import garageFooter from "../components/garage-footer.vue";
export default {
  // props: ["img"],
  data() {
    return {
      currItem: {
        title: "",
        category: "",
        desc: "",
        price: 0,
        img: "",
        condition: ""
      },
      isLoadingCat: false,
      catagories: []
    };
  },
  methods: {
    saveItem: async function() {
      var item = JSON.parse(JSON.stringify(this.currItem));
      if (this.currItem._id) {
        this.$store.dispatch({ type: "editItem", item }).then(item => {
          console.log("ret from server", item);
          
          this.$router.push(`/item/details/${item._id}`);
        });
      } else {
        var item = JSON.parse(JSON.stringify(this.currItem));

        item.createAt = Date.now();
        item.sellerId = this.$store.getters.getLoggedUser._id;
        item.location = await this.$store.dispatch({ type: "getLocation" });

        this.$store.dispatch({ type: "addItem", item }).then(itemId => {
          this.$store.commit({type:'resetNewUrl'})  
          this.$router.push(`/item/details/${itemId}`);
        });
      }
    }
  },
  computed: {},
  created() {
    var itemId = this.$route.params.id;
    if (itemId) {
      this.$store
        .dispatch({ type: "getItemById", itemId })
        .then(item => (this.currItem = item));
    } else {
      let item = {
        title: "",
        category: "",
        Description: "",
        price: 0,
        img: this.$store.getters.getImageUrl
      };
      this.currItem = item;
    }
    this.isLoadingCat = true;
    this.$store.dispatch({ type: "getAllCatagories" }).then(catagories => {
      this.catagories = catagories;
      this.isLoadingCat = false;
    });
  },
  components: {
    garageHeader,
    garageFooter
  }
};
</script>

